import React from 'react';
import './Main.css';
import Marquee from "react-fast-marquee";
import { Link } from 'react-scroll';

function Main({ windowWidth }) {
  return (
    <main className='container main' id='main'>
      {
        windowWidth > 1023 
        ?
        <>
          <p className='main__subtitle'>&nbsp; &nbsp;</p>
          <h1 className='main__title'>&nbsp;2030</h1>
        </>
        :
        <>
          <p className='main__subtitle'>   </p>
          <h1 className='main__title'> 2030</h1>
        </>
      }

      <Marquee gradient={false} speed={120} >
        <span className='main__marquee'>#</span>
        <span className='main__marquee'>  </span>
        <span className='main__marquee'> 2030</span>
        <span className='main__marquee'> </span>
        <span className='main__marquee'>#</span>
        <span className='main__marquee'>  </span>
        <span className='main__marquee'> 2030</span>
        <span className='main__marquee'> </span>
      </Marquee>
      {
        windowWidth > 1023 
        ?
        <>
        <Link className='main__button' to='programs' smooth={true} offset={-80} duration= {2000} spy={true}>   </Link>
        <Link className='main__arrow' to='description' smooth={true} offset={20} duration= {1000}></Link>
        </>
        :
        <>
        <Link className='main__button' to='programs' smooth={true} offset={-100} duration= {2000} spy={true}> </Link>
        <a className='main__button_type_enter' href='https://dpo.emiit.ru/' target='_blank' rel='noreferrer'>  </a>
        <Link className='main__arrow' to='project' smooth={true} offset={-100} duration= {1000}></Link>
        </>
      }

      <div className='main__round'></div>
    </main>
  );
}

export default Main; 